#stripe-card-element {
  padding: 6px 12px;
  margin-bottom: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s; }

#stripe-card-element.StripeElement--focus.StripeElement--invalid {
  border-color: #843534;
  box-shadow: rgba(0, 0, 0, 0.07) 0 1px 1px inset, #ce8483 0 0 6px; }

#stripe-card-element.StripeElement--focus {
  border-color: #66afe9;
  outline: 0;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6); }

#HOOK_PAYMENT #stripe-card-payment {
  padding-right: 15px;
  padding-left: 15px;
  background: #fbfbfb;
  border: 1px solid #d6d4d4;
  border-radius: 4px; }

#HOOK_PAYMENT #stripe-card-payment h3 {
  padding-bottom: 10px;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 17px;
  font-weight: bold;
  color: #000; }

#HOOK_PAYMENT #stripe-card-payment img.cc-icon {
  display: inline-block;
  width: 31px;
  margin-right: 5px;
  margin-bottom: 10px; }

#HOOK_PAYMENT #stripe-card-payment .stripe-submit-button,
#HOOK_PAYMENT .ideal-submit-button {
  position: relative;
  display: inline-block;
  width: 210px;
  padding: 5px 7px;
  margin-bottom: 15px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  background-color: #5b9bd5;
  background-image: url(../../views/img/lock-locked.png);
  background-repeat: no-repeat;
  background-position: 7px;
  background-size: 12px 17px;
  border: 1px solid rgba(77, 96, 113, 0.63);
  border-radius: 3px; }

#HOOK_PAYMENT .stripe-payment-form {
  position: relative;
  display: block;
  padding: 18px 40px 10px 20px;
  margin-bottom: 9px;
  font-size: 17px;
  font-weight: bold;
  line-height: 23px;
  color: #333;
  letter-spacing: -1px;
  cursor: pointer;
  background: #fbfbfb;
  border: 1px solid #d6d4d4;
  border-radius: 4px; }

.stripe-error-message {
  display: none;
  margin-bottom: 8px; }

.card-payment-informations {
  margin-top: 10px;
  text-align: center; }

.payment-options #stripe-card-payment {
  margin: 20px 0; }
